<template>
  <div :class="[{'wrapper':true},{'fontsS':cn},{'fonte':en}]">
    <div class="walletWrapper">
      <div :class="{'active':now=='/Wallet/ios'}" @click="changeV('/Wallet/ios')">ios</div>
      <div :class="{'active':now=='/Wallet/android'}" @click="changeV('/Wallet/android')">android</div>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  mounted() {
    this.now = this.$route.path;
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  beforeUpdate() {
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  data() {
    return {
      now: "",
      cn: true,
      en: false
    };
  },
  methods: {
    changeV(p) {
      this.$router.push(p);
      this.now = p;
    }
  }
};
</script>

<style lang="less" scoped>
.walletWrapper {
  display: flex;
  div {
    width: 5rem;
    height: 1.32rem;
    color: #9b9b9f;
    line-height: 1.32rem;
    text-align: center;
    font-size: 0.36rem;
  }
}
.active {
  background: #ff7000;
  color: #fff !important;
}
</style>